.team {
    text-align: center
}

.section {
    padding: 60px 0;
    margin-top: 9%;
}

.section-title {
    text-align: center;
    font-size: 40px;
    position: relative;
    margin-bottom: 40px;
    margin-top: 0
}

.team .team-item {
    text-align: left;
    margin-bottom: 30px
}

.team .team-item figure {
    position: relative;
    overflow: hidden
}

.imgAbout {
    max-width: 100%
}

.team .team-item figure figcaption {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    background: rgba(9, 9, 9, .6);
    -webkit-transition: all .5s ease;
    -moz-transition: all .5s ease;
    transition: all .5s ease
}


.team .team-item figure figcaption .info {
    position: absolute;
    color: #fff;
    float: left;
    bottom: 50px;
    left: 20px;
    margin-left: -80px;
    transition: all .5s ease;
}
.team .team-item figure:hover figcaption {
    opacity: 1
}

.team .team-item figure:hover figcaption .info {
    margin-left: 0
}

.team .team-item figure figcaption .info h3 {
    font-size: 20px;
    font-family: 'Noto Sans', sans-serif;
}

.team .team-item figure figcaption .info p {
    color: #fff
}

.team .team-item figure:hover figcaption .social {
    margin-bottom: 0
}

.team .team-item figure figcaption .social {
    position: absolute;
    float: right;
    bottom: 28px;
    margin-bottom: -80px;
    right: 20px;
    -webkit-transition: all .5s ease;
    -moz-transition: all .5s ease;
    transition: all .5s ease
}

.team .team-item figure figcaption .social a {
    color: #fff;
    font-size: 15px;
    width: 36px;
    height: 36px;
    background: #00606487;
    display: inline-block;
    text-align: center;
    line-height: 36px;
    border-radius: 2px
}
.about-img {
    width: 16.5rem;
}
.container-about {
    width: 75%;
    margin: 0 auto;    
}
.section-title{
    color: #006064
}
.img-logo {
    width: 3.5rem;
    margin-bottom: 2%;
}
@media (min-width: 768px){
.col-md-4 {
    flex: 0 0 24.333333%;
    max-width: 29.333333%;
}}

.title-div{
    font-family: 'Noto Sans', sans-serif;
    color:#006064;
    padding: 20px;
    padding-bottom: 66px;

}